body {
    margin: 0;
}

.body__inner {
    background-color: var(--bodyBg);
    width: 100%;
    font-family: var(--mainFont);
    font-size: var(--fontSize);
    font-weight: 400;
    color: var(--mainColor);
    line-height: 1;
    overflow: hidden;
    ul {
        list-style: none;
        margin: 0;
        padding: 0;
    }
    a {
        color: inherit;
        text-decoration: none;
        &:hover, &:focus, &:active {
            text-decoration: none;
            outline: none;
            color: inherit;
        }
    }
    div, section, a, button, p, h1, h2, h3, h4, h5, h6, blockquote, span, input {
        box-sizing: border-box;
    }
    input {
        &:focus {
            outline: none;
        }
    }
}

.title-h1,
.title-h2,
.title-h3,
.title-h4,
.title-h5,
.title-h6 {
    margin: 0;
    line-height: 1.2;
    font-family: var(--titleFont);
    text-transform: uppercase;
}
.title-h1 {
    font-size: 45px;
    text-align: center;
    font-family: "Roboto", sans-serif;
    font-weight: 700;
}
.title-h2 {
    font-size: 35px;
    font-weight: 400;
    margin-bottom: 165px;
    text-align: center;
    span {
        font-weight: 700;
    }
}
.title-h3 {
    font-size: 22px;
    text-align: center;
    font-weight: 700;
}
.title-h4 {
    font-size: 18px;
    font-weight: 400;
}
@media(max-width: 1450px) {
    .title-h1 {
        font-size: 35px;
    }
}
@media(max-width: 900px) {
    .title-h2 {
        margin-bottom: 25px;
    }
}
@media(max-width: 767px) {
    .title-h1, .typography h1 {
        font-size: 30px;
    }
    .title-h2, .typography h2 {
        font-size: 28px;
    }
    .title-h3, .typography h3, .banner__item-big .title-h1, .banner__item-big .title-h1 span {
        font-size: 18px;
    }
    .title-h4, .typography h4 {
        font-size: 15px;
    }
}
@media(max-width: 575px) {
    .title-h1, .typography h1 {
        font-size: 28px;
    }
    .title-h2, .typography h2 {
        font-size: 26px;
    }
}